//
// typo
// ----------------------------------------------------------------


body {
  @include rwd-typo-size(body);
  background-color: $gray-10;
    padding: .5em;
  @include media-breakpoint-up(medium) {
    padding: 1em;
  }
}

pre {
  padding: .5em;
}

h1 {
  color: $body-color;
  position: relative;
  margin-bottom: .7em;
  &::after {
    content: " ";
    position: absolute;
    bottom: -.35em;
    left: 0em;
    width: 1.8em;
    height: 3px;
    margin-left: 0em;
    background: $primary-color;
  }
}

.article-header {
  padding: 0 1.5em 2em;
  h1 {
    margin-bottom: .2em;
  }
  &.article-header-home {
    h1 {
      font-size: 1.6em;
    }
  }
}

.article {
  h2 {
    margin-top: 1.25em;
    &:first-child {
      margin-top: 0;
    }
  }
  p + ul {
    margin-top: -.7em;
  }
  ol li,
  ul li {
    margin-bottom: .25em;
  }
  ol ol,
  ul ul,
  ol ul,
  ul ol {
    padding-top: .25em;
    padding-bottom: .25em;
  }
}


blockquote,
.blockquote {
  @include blockquote;
  font-size: 1em;
  p {
    margin-bottom: .5em;
  }
  &.blockquote-lg {
  font-size: 1.2em;
  }
}

table {
  @include table();
  @include table-sm();
}


.article-aside {
  a {
    color:$primary-color-dark;
  }
  a:focus,
  a:hover {
    color:$body-color;
  }
  .contribute {
    margin-bottom: 0;
  }

}

.aside-toc {
  background-color: $gray-9;
  padding: 1em 1em;
  margin-bottom: 1em;

  ul {
    padding:0;
    margin: 0em;
    li {
      margin: .45em 0;
      list-style-type: none;
      li {
        list-style-type: disc;
        margin-left: 1em;
        li {
          list-style-type: circle;
        }
      }
    }
  }
}

.aside-home {
  p {
    margin-bottom: .2em;
  }
  ul li {
    margin: .45em 0;
  }
}


.info-doc {
  p {
    margin-bottom: .2em;
  }
}

