/*! layout */

.container {
  @include rwd-block-width(container);
  padding: 0;
}

.main-container.main-container {
  // padding-top: 3em;
  // padding-bottom: 3em;
  background: #fff;
  padding: 0;
}

.main {
  padding-top: 2em;
  padding-bottom: 1em;
  min-height: calc(100vh - 25em);
}


.article-aside {
  padding-top: 4em;
  padding: 1em 1em;
}

.one-column {
  max-width: 48em;
  margin: 0 auto;
  background-color: #fff;
  // padding: 1em;

}

.two-columns {

  .article {
    padding: 1em 1em;
  }

  .article-aside-home {
    background-color: $gray-9;
    margin: 0em .0em 0.5em;
    padding: 1em 1.5em;
  }

  @include media-breakpoint-up(medium) {
    display: flex;
    justify-content: space-around;

    .article-header {
      // flex:5;
      padding: 1em 1.5em;
    }

    .article {
      flex:5;
      padding: 0.6em 1.5em 1em;
    }

    .article-aside {
      // float: right;
      // order:3;
      order:2;
      flex:3;
      padding: 0em 1em;

    }
    .article-aside-home {
      // margin: 1em 1em 1.5em 1.5em;
    }
  }
}

.contribute {}
