// menu horizontal simple
// replié sur mobile et tablette
// ---------------------------------------------------------------------------

$menu-simple-bg-color:          #fff;
// $menu-simple-text-color:        $primary-color;
$menu-simple-text-color:        $body-color;

$menu-simple-bg-color-hover:    $primary-color-dark;
$menu-simple-text-color-hover:  #fff;

$menu-simple-bg-color-open:     $primary-color;
$menu-simple-text-color-open:   #fff;


$toggle-menu-bg-color:          $primary-color;
$toggle-menu-text-color:        #fff;

$toggle-menu-bg-color-hover:    $primary-color-dark;
$toggle-menu-text-color-hover:  #fff;

$toggle-menu-bg-color-open:     $primary-color-dark;
$toggle-menu-text-color-open:   #fff;

// @import "../../scampi/modules/menu-simple/menu-simple";

// menu horizontal simple
// replié sur mobile et tablette
// ---------------------------------------------------------------------------

.nav-main {
  background-color: $menu-simple-bg-color;
  color: $menu-simple-text-color;

  .nav-main-list {
    display: none;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .nav-link {
    position: relative;
    display: block;
    width: 100%;
    padding: .7em 1em .8em;
    color: $menu-simple-text-color;
    text-decoration: none;
    // transition: .2s;
    // transition-timing-function: ease;
    // font-size: .875em;
    // font-weight: bold;

    abbr[title] {
      border-bottom: 0;
    }


    &.is-active {
      background-color: $menu-simple-bg-color-open;
      color: $menu-simple-text-color-open;
      // font-weight: bold;

      &:after {
        content: " ";
        position: absolute;
        bottom: .35em;
        left: 1em;
        width: 1.4em;
        height: 2px;
        margin-left: 0em;
        background: #fff;
      }
      &:hover {
        background-color: $menu-simple-bg-color-open;
      }
    }
    @include media-breakpoint-up(medium) {
      &.is-active {
        &:after {
          left: 50%;
          margin-left: -.7em;
        }
      }
    }

    &:hover,
    &:focus {
      background-color: $menu-simple-bg-color-hover;
      color: $menu-simple-text-color-hover;
    }

  }
}

.nav-main-list.is-open {
  display: block;
}

// si pas de js, pas de bouton
.no-js .toggle-menu {
  display: none;
}

// si pas de js, éléments du menu affichés
.no-js .nav-main-list {
  display: block;
}

// à partir de la taille medium, le menu est affiché à l'horizontale
@include media-breakpoint-up(medium) {
  .nav-main {
    border: 1px solid $primary-color;
    border-width: 3px 0;
    margin: 0 1em;
    .nav-main-list {
      display: table;
      width: 100%;
      // display: flex;
      // justify-content: space-between;
      padding: 0;
      list-style-type: none;
      color: $menu-simple-text-color;
      background-color: $menu-simple-bg-color;
      text-align: center;

      .nav-item {
        display: table-cell;
        line-height: 1;
      }
    }
  }
}

// bouton du menu
.toggle-menu {
  width: 100%;
  padding: .5em 1em;
  border: none;
  margin: 0;
  background-color: $toggle-menu-bg-color;
  color: $toggle-menu-text-color;
  text-align: left;

  &:before {
    display: inline-block;
    content: "\2261";
    font-size: 1.5em;
    padding-right: .5em;
    line-height: 1;
    speak: none;
    vertical-align: bottom;
  }

  &:hover,
  &:focus {
    background-color: $toggle-menu-bg-color-hover;
    color: $toggle-menu-text-color-hover;
  }

  &.is-open {
    background-color: $toggle-menu-bg-color-open;
    color: $toggle-menu-text-color-open;

    &:before {
      content: "\00D7";
    }

    &:focus {
      border: 1px dotted #fff;
    }
  }

  @include media-breakpoint-up(medium) {
    display: none;
  }
}
