$border-color-collapse-focus: $link-color;
$expand-symbol-closed: '▶';
$expand-symbol-open: $expand-symbol-closed;

@import "../../scampi/modules/collapse/style-collapse";

.expandmore__button {
  text-align: left;
  padding: 0.2em 0;

  position: relative;
  .expand-sign:before {
    content: none;
  }

}

.question {
  margin-bottom: 1.3em;
  h3 {
    margin: 0;
    font-size: 1.1em;
  }
  h4 {
    font-size: 1.1em;
    background-color: $gray-10;
    padding: .1em .2em;
    display: inline-block;
  }

  .expandmore__button {
    border-left: 3px solid $gray-9;
    // border-left: 3px solid #fff;
    padding: 0.3em 1.2em 0.3em 1.2em;

    display: block;
  }

  .expandmore__symbol {
    display: none;
  }


  .expandmore__button[aria-expanded=true] {
    border-color: $primary-color;
    background-color: $gray-10;

    .expandmore__symbol {
      display: block;
    }

    .expandmore__symbol:after,
    .expandmore__symbol:before {
      position: absolute;
      top:.6em;
      left:-2px;
      color: $primary-color;
      line-height: 1;
    }
    .expandmore__symbol:after {
      left:-7px;
      content: $expand-symbol-closed;
      color: #fff;
    }

  }

  .js-to_expand {
    padding: .5em 1em 0.5em 1.2em;
    border-left: 3px solid $primary-color;
    background-color: $gray-13;
    :last-child {
      margin: 0;
    }
  }

}



