// Fontes utilisées
// map pour module fontface
// ==========================================================

// variables projet
$enable-fontface: false;

$fonts-map: (
  'OpenSans': ( // sera utilisé comme valeur pour la font-family de ce groupe
    (
      'font-path'   : "opensans/opensans_light/OpenSans-Light",
      'font-weight' : 300,
      'font-style'  : normal
    ) ,
    (
      'font-path'   : "opensans/opensans_regular/OpenSans-Regular",
      'font-weight' : 400,
      'font-style'  : normal
    ) ,
    (
      'font-path'   : "opensans/opensans_semibold/OpenSans-Semibold",
      'font-weight' : 600,
      'font-style'  : normal
    ) ,
    (
      'font-path'   : "opensans/opensans_light-italic/OpenSans-LightItalic",
      'font-weight' : 300,
      'font-style'  : italic
    ),
    (
      'font-path'   : "opensans/opensans_italic/OpenSans-Italic",
      'font-weight' : 400,
      'font-style'  : italic
    )
  )
);

$webfont-path: "../fonts/";
$opensans: OpenSans, arial, sans-serif;
$system: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

$font-family-base: $system;

// import module scampi
@import "../../scampi/modules/fontface/fontface";
