//
// banner
// ----------------------------------------------------------------


.site-banner {
  margin: 0 1em;
  padding: .5em 0;

  p {
    margin-bottom: 0;
  }

  .site-banner-main {
    display: inline-block;
    // background: $primary-color;
    // color: #fff;
    // padding: 0.5em 1em;
    // padding: 0.5em 0em;
    // margin: 0 -.5em;

  }
  .site-title {
    // font-size: 1.5em;
    // padding-bottom: .6em;
    line-height: 1.3;
    strong {
      font-weight: normal;
    }
    a {
      font-size: 2.2em;
      font-size: 1.7em;
      // color: #fff;
      text-decoration: none;
      // margin-left: -0.3em;
      // border-bottom: .1em solid #fff;
      &:focus,
      &:hover {
        text-decoration: underline;
      }

    }


  }

  // .logo {
  //   text-align: left;
  //   img {
  //     max-width:100%;
  //     height: auto;
  //   }
  // }

  .site-banner-note {
    padding-top: .5em;
    font-size: .9em;
  }


  @include media-breakpoint-up(medium) {
    display: flex;
    justify-content: space-between;
    padding: 1.2em 0 1.2em;
    margin: 0 1.5em;

    .site-banner-main {

    }
    .site-banner-note {
      align-self: flex-end;
      text-align: right;
    }
    .site-title {
      a {
        font-size: 2em;
      }
    }
  }

}


