//
// footer
// ----------------------------------------------------------------

.site-footer {
  // min-height: 10em;

  a:hover {
    text-decoration: underline;
  }


  .footer-links {
    background-color: $primary-color;
    color: #fff;
    padding: .4em 0;

    a,
    a:focus,
    a:hover {
      text-decoration: none;
      color: #fff;
      img {
        margin-left: .25em;
      }
    }
    a:focus,
    a:hover {
      text-decoration: underline;
    }


    .container {
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include media-breakpoint-up(small) {
        flex-direction: row;
      }
    }

    ul {
      margin: 0 auto;
      text-align: center;
      list-style-type: none;
    }

    li {
      display: inline;

      a {
        color: #fff;
        font-size: 0.875em;
        padding:0 1em;

        @include media-breakpoint-up(small) {
          border-right: 1px solid #fff;
        }
      }

      &:last-child a {
        border: none;
      }
    }
  }

  .big-footer {
    padding: 1em 1.5em;

    background-color: $gray-8;
    // background-color: $gray-5;
    // color: #fff;

    @include media-breakpoint-up(medium) {
      .big-footer-inner {
        // max-width: 50em;
        // margin: 0 auto;
        display: flex;
        // justify-content: space-around;
        // justify-content: space-between;
      }
      .info-doc {
        min-width: 20em;
      }
    }
  }
}
