  padding: 1em 1.5em;
  background-color: #d1d1d8;
}

@media (min-width: 48em) {
  .site-footer .big-footer .big-footer-inner {
    display: flex;
  }
  .site-footer .big-footer .info-doc {
    min-width: 20em;
  }
}

/*! layout */
.container {
  max-width: 100%;
  padding: 0;
}

@media (min-width: 70em) {
  .container {
    max-width: 70em;
  }
}

.main-container.main-container {
  background: #fff;
  padding: 0;
}

.main {
  padding-top: 2em;
  padding-bottom: 1em;
  min-height: calc(100vh - 25em);
}

.article-aside {
  padding-top: 4em;
  padding: 1em 1em;
}

.one-column {
  max-width: 48em;
  margin: 0 auto;
  background-color: #fff;
}

.two-columns .article {
  padding: 1em 1em;
}

.two-columns .article-aside-home {
  background-color: #dfdfe4;
  margin: 0em .0em 0.5em;
  padding: 1em 1.5em;
}

@media (min-width: 48em) {
  .two-columns {
    display: flex;
    justify-content: space-around;
  }
  .two-columns .article-header {
    padding: 1em 1.5em;
  }
  .two-columns .article {
    flex: 5;
    padding: 0.6em 1.5em 1em;
  }
  .two-columns .article-aside {
    order: 2;
    flex: 3;
    padding: 0em 1em;
  }
}

body {
  font-size: 0.875em;
  background-color: #ecedef;
  padding: .5em;
}

@media (min-width: 34em) {
  body {
    font-size: 1em;
  }
}

@media (min-width: 48em) {
  body {
    padding: 1em;
  }
}

pre {
  padding: .5em;
}

h1 {
  color: #3e3c3d;
  position: relative;
  margin-bottom: .7em;
}

h1::after {
  content: " ";
  position: absolute;
  bottom: -.35em;
  left: 0em;
  width: 1.8em;
  height: 3px;
  margin-left: 0em;
  background: #0081D5;
}

.article-header {
  padding: 0 1.5em 2em;
}

.article-header h1 {
  margin-bottom: .2em;
}

.article-header.article-header-home h1 {
  font-size: 1.6em;
}

.article h2 {
  margin-top: 1.25em;
}

.article h2:first-child {
  margin-top: 0;
}

.article p + ul {
  margin-top: -.7em;
}

.article ol li,
.article ul li {
  margin-bottom: .25em;
}

.article ol ol,
.article ul ul,
.article ol ul,
.article ul ol {
  padding-top: .25em;
  padding-bottom: .25em;
}

blockquote,
.blockquote {
  padding: 0.5em 1em;
  margin: 0 0 1em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.25em;
  font-style: normal;
  border-left: 5px solid #d1d1d8;
  font-size: 1em;
}

blockquote footer,
.blockquote footer {
  display: block;
  font-size: .75em;
  line-height: 1.5;
  color: #42434e;
}

blockquote footer a:link,
blockquote footer a:visited,
.blockquote footer a:link,
.blockquote footer a:visited {
  color: #42434e;
}

blockquote footer p,
.blockquote footer p {
  margin-bottom: 0;
}

blockquote p,
.blockquote p {
  margin-bottom: .5em;
}

blockquote.blockquote-lg,
.blockquote.blockquote-lg {
  font-size: 1.2em;
}

table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1em;
  background-color: transparent;
}

table caption {
  padding-top: 0.75em;
  padding-bottom: 0.75em;
  text-align: left;
  color: #3e3c3d;
}

table th {
  text-align: left;
}

table th,
table td {
  padding: 0.75em;
  line-height: 1.5;
  vertical-align: top;
  border-top: 1px solid #dfdfe4;
}

table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dfdfe4;
}

table tbody + tbody {
  border-top: 2px solid #dfdfe4;
}

table th,
table td {
  padding: 0.25em;
}

.article-aside a {
  color: #0062a2;
}

.article-aside a:focus,
.article-aside a:hover {
  color: #3e3c3d;
}

.article-aside .contribute {
  margin-bottom: 0;
}

.aside-toc {
  background-color: #dfdfe4;
  padding: 1em 1em;
  margin-bottom: 1em;
}

.aside-toc ul {
  padding: 0;
  margin: 0em;
}

.aside-toc ul li {
  margin: .45em 0;
  list-style-type: none;
}

.aside-toc ul li li {
  list-style-type: disc;
  margin-left: 1em;
}

.aside-toc ul li li li {
  list-style-type: circle;
}

.aside-home p {
  margin-bottom: .2em;
}

.aside-home ul li {
  margin: .45em 0;
}

.info-doc p {
  margin-bottom: .2em;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.5em;
  padding-right: 0.5em;
}

.container::after {
  content: "";
  display: table;
  clear: both;
}

.main-container {
  padding-top: 2em;
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.list-inline {
  padding-left: 0;
  list-style: none;
}

.list-inline > li {
  display: inline-block;
}

.list-inline > li:not(:last-child) {
  margin-right: 0.25em;
}

.list-inline-item {
  display: inline-block;
}

.list-inline-item:not(:last-child) {
  margin-right: 0.25em;
}

.lead {
  font-size: 1em;
  font-weight: 600;
}

.text-nowrap {
  white-space: nowrap !important;
}

.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-muted {
  color: #717285;
}

.initialism {
  font-size: 90%;
  text-transform: uppercase;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  white-space: nowrap;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
  -webkit-clip-path: none;
  clip-path: none;
  white-space: normal;
}

.img-fluid {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  padding: 0;
}
